// Header
.header {
    padding: $base-line-height - 5px 0;
    font-family: $Montserrat-font;

    &__inner {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
    }

    &__time {
        position: relative;
        padding-left: 27px;
        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: 0;
            display: block;
            width: 18px;
            height: 18px;
            background-image: url(../img/svg/clock.svg);
            background-repeat: no-repeat;
            transform: translateY(-50%);
        }
    }

    &__mail {
        position: relative;
        padding-left: 27px;
        &::before {
            content: "";
            position: absolute;
            top: 2px;
            left: 0;
            display: block;
            width: 17px;
            height: 13px;
            background-image: url(../img/svg/mail.svg);
            background-repeat: no-repeat;
        }
    }

    &__social-link {
        margin-right: $base-line-height;
    }

    &__social-link:last-child {
        margin-right: 0;
    }

    &__social-vk {
        margin-right: 20px;
        display: inline-block;
        width: 18px;
        margin-top: 4px;

        &:hover {
            filter: invert(35%) sepia(6%) saturate(2050%) hue-rotate(101deg) brightness(99%) contrast(85%);
        }
    }

    &__social-insta {
        display: inline-block;
        width: 18px;

        &:hover {
            filter: invert(35%) sepia(6%) saturate(2050%) hue-rotate(101deg) brightness(99%) contrast(85%);
        }
    }

    &__tel {
        position: relative;
        padding-left: 27px;
        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            display: block;
            width: 18px;
            height: 18px;
            background-image: url(../img/svg/tel.svg);
            background-repeat: no-repeat;
        }
    }

    &__tel-link,
    &__mail-link {
        color: $base-black-color;
        transition: $base-transition;
        &:hover {
            color: $base-green-color;
        }
    }

    &__button {
        color: $base-green-color;
        background-color: $base-white-color;

        &:hover {
            color: $base-white-color;
            background-color: $base-green-color;
        }
    }

    @media all and (max-width: 991px) {
        &__inner {
            justify-content: space-around;
        }

        &__social-link {
            margin-right: 0;
        }
    }

    @media all and (max-width: 575px) {
         &__time,
         &__mail,
         &__social,
         &__button {
          display: none;
        }

       

        &__logo {
            position: fixed;
            top: 17px;
            right: 15px;
            width: 60px;
            z-index: 1110;
        }

        &__tel {
            position: fixed;
            top: 27px;
            left: 34%;
            z-index: 1110;
            &::before {
                left: 5px;
                filter: invert(100%) sepia(0%) saturate(2%) hue-rotate(349deg) brightness(106%) contrast(101%);
            }
        }

        &__tel-link {
            color: $base-white-color;
        }
    }

    @media all and (max-width: 359px) {
        &__tel {
            left: 20%;
        }
    }
}
